.avatar {
	position: relative;

	overflow: hidden;

	padding-top: 100%;

	background: url(circle.svg);
	background-repeat: no-repeat;
}

img {
	position: absolute;
	bottom: 0;
	left: 0;

	-webkit-box-sizing: border-box;

	        box-sizing: border-box;
	width: 100%;
	height: auto;
	padding: 8%;

	border-radius: 100%;
}



